import React, {Component} from 'react'

class Login extends Component{
    render(){
        const {handleUserPage, handleLogin} = this.props
        
        return(
            <div>
                <div className="card login-content">
                    <h1>Xin chào Gapoer!</h1>
                </div>
                <form className="card login-form"
                    onSubmit={async (event)=>{
                        event.preventDefault()
                        handleLogin(document.getElementById('email').value, document.getElementById('password').value)
                    }}>
                    <input id="email" type="text" className="form-control mb-3" placeholder="Email" />
                    <input id="password" type="password" className="form-control mb-3" placeholder="Mật khẩu" />
                    <button className="form-control mb-3 bg-primary cursor-pointer">Đăng nhập</button>
                    <div className="text-center">                        
                        <a href="" className="text-primary">Quên mật khẩu?</a>
                    </div>
                    <div className="border mb-3 mt-3"></div>
                    <input onClick={()=>{handleUserPage(false)}} type="button" className="form-control bg-success cursor-pointer" value="Đăng ký"/>
                </form>
            </div>
        )
    }
}

export default Login