import React, { Component } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faGlobeAsia } from "@fortawesome/free-solid-svg-icons";
import GetGroupByID from "../../../../GetGroupByID";

import ShowGroupsBtn from "./ShowGroupsBtn/Index";
import GroupsList from "./GroupsList/Index";
import GroupSelected from "./GroupSelected/Index";
import axios from "axios";
import { URL } from "../../../../../constants/url";

class PostBox extends Component {
  constructor() {
    super();

    this.state = {
      isShowGroupsList: false,
      isSelectedGroup: false,
      selectedGroupID: "",
      selectedGroup: {},
    };

    this.handleSelectedGroup = this.handleSelectedGroup.bind(this);
    this.handlePost = this.handlePost.bind(this);
    this.handleGroupsList = this.handleGroupsList.bind(this);
  }

  async componentDidUpdate(prevProps, prevState) {
    if (prevState.selectedGroupID !== this.state.selectedGroupID) {
      const group = await GetGroupByID(this.state.selectedGroupID);
      if (group.status === "success") {
        this.setState({
          isSelectedGroup: true,
          selectedGroup: group.doc,
        });
      }
    }
  }

  handlePost() {
    const { userInfo } = this.props;
    const content = document.getElementById("content").value;
    const groupID = this.state.selectedGroupID;
    const timestamps = new Date().getTime();

    if (content && userInfo._id) {
      axios
        .post(`${URL}/posts/`, {
          userID: userInfo._id,
          content,
          timestamps,
          reaction: {
            like: [],
            love: [],
            haha: [],
            wow: [],
            sad: [],
            angry: [],
          },
          comments: [],
        })
        .then((res) => res.data)
        .then((data) => {
          if (data.status === "success") {
            if (groupID) {
              fetch(`http://localhost:3001/groups/${groupID}`, {
                method: "PUT",
                headers: { "content-type": "application/json" },
                body: JSON.stringify({
                  postID: data.doc._id,
                }),
              });
            }
          }

          return data.doc;
        })
        .then((doc) => {
          this.props.isCreatePost(doc);
        });
    }
  }

  handleSelectedGroup(id) {
    if (!id) {
      this.setState({
        isSelectedGroup: false,
      });
    }

    this.setState({
      selectedGroupID: id,
    });
  }

  handleGroupsList() {
    this.setState({
      isShowGroupsList: !this.state.isShowGroupsList,
    });
  }

  render() {
    return (
      <div className="card-min-width p-2 d-flex flex-column position-absolute bg-light rounded box-shadow z-index-9">
        <div className="d-flex flex-nowrap align-items-center">
          <img
            className="mr-3 circle border"
            src="https://www.gapo.vn/assets/images/default-user-avatar.png"
            alt=""
          />
          <div>
            <div className="fw-6 text-nowrap mb-2 username">Xuân Minh</div>
            <div className="p-5 d-flex justify-content-center align-items-center rounded bg-color-light-2 fs-07 text-nowrap text-secondary cursor-pointer">
              <FontAwesomeIcon icon={faGlobeAsia} className="mr-5 fs-1" />
              Công khai
              <img
                src="https://www.gapo.vn/assets/icons/icon-dropdown.svg"
                alt=""
              />
            </div>
          </div>
        </div>

        <div className="my-3 border"></div>

        <div>
          <textarea
            id="content"
            className="w-100 fs-1 outline-none border-0"
            cols="50"
            rows="10"
            placeholder="Bạn muốn chia sẻ điều gì?"
          ></textarea>
        </div>

        {this.state.isSelectedGroup ? (
          <GroupSelected groupInfo={this.state.selectedGroup} />
        ) : (
          ""
        )}

        {this.state.isShowGroupsList ? (
          <GroupsList handleSelectedGroup={this.handleSelectedGroup} />
        ) : (
          ""
        )}

        <div className="mt-3 border"></div>

        <div className="d-flex p-1">
          <div className="py-3 col d-flex justify-content-center align-items-center col rounded cursor-pointer hover-gray">
            <img
              className="mr-2"
              width="20px"
              src="https://www.gapo.vn/assets/images/svg-icons/picture-video.svg"
              alt=""
            />
            Ảnh/Video
          </div>
          <div className="py-3 col d-flex justify-content-center align-items-center col rounded cursor-pointer hover-gray">
            <img
              className="mr-2"
              width="20px"
              src="https://www.gapo.vn/assets/icons/icon-ask.svg"
              alt=""
            />
            Hỏi đáp
          </div>

          <ShowGroupsBtn handleGroupsList={this.handleGroupsList} />
        </div>

        <div className="mb-3 border"></div>

        <div>
          <button
            onClick={() => {
              this.handlePost();
            }}
            id="post-btn"
            className="w-100 p-3 bg-color-green-2 fs-1 text-light rounded outline-none border-0 cursor-pointer transition-03"
          >
            Chia sẻ
          </button>
        </div>
      </div>
    );
  }
}

export default PostBox;
