import React, {Component} from 'react'

class NoMessageInfo extends Component{
    render(){
        const {friendInfo} = this.props

        return(
            <div className='d-flex flex-column align-items-center'>
                <img src={friendInfo.avatar} className="rounded-circle border" style={{height: '80px', width: '80px'}}/>
                <div className="username my-2">{friendInfo.firstName} {friendInfo.lastName}</div>
                <p className="fs-08 text-secondary">Các bạn là bạn bè trên Gapo</p>
            </div>
        )
    }
}

export default NoMessageInfo