import React, {Component} from 'react'

class UserCard extends Component{
    render(){
        const {handleLogin, userInfo} = this.props

        return(
            <div className="position-absolute top-100 border rounded box-shadow header-user-card">
                <div className="p-5 d-flex flex-column">
                    <img className="mx-auto header-user-card--avatar rounded-circle border" src={userInfo.avatar} alt="" />
                    <p className="mx-auto my-3 fw-6 username">{userInfo.firstName} {userInfo.lastName}</p>
                    <a className="text-center text-secondary" href="#">Trang cá nhân</a>
                    <div className="border my-5"></div>
                    <div className="p-3 d-flex align-items-center rounded cursor-pointer transition-03 hover-gray" onClick={()=>{handleLogin(false)}}>
                        <img className="mx-3" src="https://www.gapo.vn/assets/icons/icon-logout.svg" alt="" />
                        Đăng xuất
                    </div>
                </div>
            </div>
        )
    }
}

export default UserCard