import React, {Component} from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faCheck } from '@fortawesome/free-solid-svg-icons'
import UpdateUserByID from '../../../../../../../UpdateUserByID'

class Responding extends Component{
    constructor(){
        super()
        this.setFriendStatus = this.setFriendStatus.bind(this)
    }

    setFriendStatus(){
        const {userID, friendID} = this.props

        UpdateUserByID(userID, 'friends', {
            _id: friendID,
            status: true
        })

        UpdateUserByID(friendID, 'friends', {
            _id: userID,
            status: true
        })
    }

    render(){
        const {handleFriendStatus} = this.props

        return(
            <span className="ml-auto bg-color-green-1 text-green rounded transition-03 add-friend--btn" style={{border: '.5px #6fbe44 solid'}}>
                <div onClick={async ()=>{
                    await this.setFriendStatus()
                    handleFriendStatus(true)
                }} className="p-3 fs-08 text-nowrap" style={{padding: '8px'}}>
                    <div>
                        <FontAwesomeIcon icon={faCheck} style={{marginRight: '5px'}}/>
                        Xác nhận
                    </div>
                </div>
            </span>
        )
    }
}

export default Responding