import React, {Component} from 'react'

class PostReaction extends Component{
    render(){
        const {post} = this.props

        return(
            <div className="d-flex cursor-pointer">
                <img className="mr-3 circle-auto" src="http://gapo.vn/assets/images/png-icons/like-reaction.png" alt="" />

                <div className="d-flex align-items-center">
                    {post.reaction.like.length + post.reaction.love.length + post.reaction.haha.length + post.reaction.wow.length + post.reaction.sad.length + post.reaction.angry.length}
                </div>
            </div>
        )
    }
}

export default PostReaction