import React, {Component} from 'react'
import InfiniteScroll from 'react-infinite-scroll-component'
import GetGroupByID from '../../../GetGroupByID'
import GetPostByID from '../../../GetPostByID'
// import { faTimes } from '@fortawesome/free-solid-svg-icons'
// import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

import PostBox from './PostBox/Index'
import PostItem from './PostItem/Index'
import PostButton from './PostButton/Index'
import Tendency from './Tendency/Index'
import GroupBox from './GroupBox/Index'
import LoadingItem from './LoadingItem/Index'

class Content extends Component{
    constructor(props){
        super(props)

        this.state = {
            groupID: props.groupSelected,
            menuSelected: '',
            isShowPostBox: false,
            isShowGroupBox: false,
            isCreatePost: false,
            isCreateGroup: false,
            postsList: [],
            startPost: 0,
            postNum: 3,
            hasMore: true
        }

        this.handleTimeOut = 0

        this.handlePostBox = this.handlePostBox.bind(this)
        this.isCreatePost = this.isCreatePost.bind(this)
        this.isCreateGroup = this.isCreateGroup.bind(this)
        this.handlePost = this.handlePost.bind(this)
    }

    UNSAFE_componentWillMount(){
        this.setState({
            menuSelected: this.props.menuSelected
        })

        this.getPosts(this.state.groupID)
    }

    async componentDidUpdate(prevProps, prevState){
        if(prevProps.isShowGroupBox !== this.props.isShowGroupBox){
            this.setState({
                isShowGroupBox: this.props.isShowGroupBox
            })
        }

        if(prevState.isCreatePost !== this.state.isCreatePost){
            await this.getPosts(this.props.groupID)
            this.setState({
                isCreatePost: false
            })
        }
    }

    UNSAFE_componentWillReceiveProps(nextProps){
        if(nextProps.groupSelected !== this.props.groupSelected){
            this.setState({
                hasMore: true,
                postsList: [],
                startPost: 0,
                postNum: 3,
            })
            this.getPosts(nextProps.groupSelected)
        }
    }

    handlePostBox(status){
        this.setState({
            isShowPostBox: status
        })
    }

    async getPosts(groupID){
        if(!groupID){
            const list = await fetch('http://localhost:3001/posts').then(res => res.json())
            if(list.status === 'success'){
                if(list.doc.length === 0){
                    this.setState({
                        hasMore: false
                    })

                    return
                }

                let i = this.state.startPost
                while(i < this.state.postNum){
                    if(list.doc[i]){
                        this.setState({
                            postsList: [...this.state.postsList, list.doc[i]]
                        })
        
                        i++
                        continue
                    }
    
                    this.setState({
                        startPost: this.state.postsList.length,
                        postNum: i + 3
                    })
    
                    break
                }
    
                this.setState({
                    startPost: this.state.postsList.length,
                    postNum: this.state.postsList.length + 3
                })
            }
        }

        if(groupID){
            const group = await GetGroupByID(groupID)
            if(group.status === 'success'){
                if(group.doc.posts.length === 0){
                    this.setState({
                        hasMore: false
                    })

                    return
                }

                let i = this.state.startPost
                while(i < this.state.postNum){
                    if(group.doc.posts[i]){
                        const post = await GetPostByID(group.doc.posts[i])
                        if(post.status === 'success'){
                            this.setState({
                                postsList: [...this.state.postsList, post.doc]
                            })
                        }
        
                        i++
                        continue
                    }
    
                    this.setState({
                        startPost: this.state.postsList.length,
                        postNum: i + 3
                    })
    
                    break
                }
    
                this.setState({
                    startPost: this.state.postsList.length,
                    postNum: this.state.postsList.length + 3
                })
            }
        }
    }

    handlePost(id){
        setTimeout(async()=>{
            if(!id){
                const list = await fetch('http://localhost:3001/posts').then(res => res.json())
                if(list.status === 'success'){
                    let i = this.state.startPost
                    while(i < this.state.postNum){
                        if(list.doc[i]){
                            this.setState({
                                postsList: [...this.state.postsList, list.doc[i]]
                            })
            
                            i++
                            continue
                        }
    
                        this.setState({
                            hasMore: false
                        })
    
                        break
                    }
        
                    if(this.state.hasMore){
                        this.setState({
                            startPost: this.state.postsList.length + 1,
                            postNum: this.state.postsList.length + 4
                        })
                    }
                }
            }

            if(id){
                const group = await GetGroupByID(id)
                if(group.status === 'success'){
                    if(group.doc.posts.length === 0){
                        this.setState({
                            hasMore: false
                        })

                        return
                    }

                    let i = this.state.startPost
                    while(i < this.state.postNum){
                        if(group.doc.posts[i]){
                            const post = await GetPostByID(group.doc.posts[i])
                            if(post.status === 'success'){
                                this.setState({
                                    postsList: [...this.state.postsList, post.doc]
                                })
                            }
            
                            i++
                            continue
                        }
        
                        this.setState({
                            startPost: this.state.postsList.length,
                            postNum: i + 3
                        })
        
                        break
                    }
        
                    this.setState({
                        startPost: this.state.postsList.length,
                        postNum: this.state.postsList.length + 3
                    })
                }
            }
        }, 1500)
    }

    isCreatePost(post){
        this.setState({
            postsList: [post, ...this.state.postsList],
            postNum: this.state.postsList.length + 1,
            startPost: this.state.postsList.length + 3,
            isCreatePost: true,
            isShowPostBox: false
        })
    }

    isCreateGroup(){
        this.setState({
            isCreateGroup: true,
            isShowGroupBox: false
        })

        this.props.handleCreateGroup()
    }

    render(){
        const {userInfo, handleShowGroupBox, handleAlert, groupSelected} = this.props

        return(
            <div className="p-1 d-flex flex-column align-items-center col-lg-6 col-md-8 col-12 position-relative main-content">
                {this.state.isShowPostBox ? <PostBox userInfo={userInfo} isCreatePost={this.isCreatePost} /> : ''}

                {this.state.isShowGroupBox ? <GroupBox userInfo={userInfo} handleAlert={handleAlert} isCreateGroup={this.isCreateGroup}/> : ''}

                <PostButton userAvatar={userInfo.avatar} handlePostBox={this.handlePostBox}/>

                <Tendency />

                <div className="card-min-width col-8">
                    <InfiniteScroll
                            dataLength={this.state.postsList.length}
                            next={()=>{this.handlePost(groupSelected)}}
                            hasMore={this.state.hasMore}
                            loader={
                                <LoadingItem />
                            }
                            style={{overflow: 'visible'}}
                            className='post-items'
                    >
                        {this.state.postsList.length > 0 ? this.state.postsList.map(post => <PostItem key={post._id} id={post._id} userInfo={userInfo} />) : ''}
                    </InfiniteScroll>
                </div>

                {this.state.isShowPostBox || this.state.isShowGroupBox ? <div onClick={()=> {
                    this.handlePostBox(false)
                    handleShowGroupBox(false)
                }} className="screen--dark"></div> : ''}
            </div>
        )
    }
}

export default Content