import React, {Component} from 'react'

class Friend extends Component{
    render(){
        const {userInfo, handleFriendMessage} = this.props

        return(
            <div onClick={()=>{handleFriendMessage(userInfo._id)}} className="mb-1 p-2 d-flex align-items-center rounded hover-gray cursor-pointer friend-message">
                <img className="circle mr-3 border" src={userInfo.avatar} alt="" />
                <span className="fw-6">{userInfo.firstName} {userInfo.lastName}</span>
            </div>
        )
    }
}

export default Friend