import React, {Component} from 'react'

class TendencyItem extends Component{
    render(){
        const {item} = this.props

        return(
            <li className="position-relative">
                <div className=" border rounded post-tendency-item">
                    <img className="w-100 rounded post-tendency-item-image" src={item.img} alt="" />
                </div>
                <p className="w-100 px-5 position-absolute bottom-10px left-50 translate-x-50 fw-7 text-light text-center text-overflow-ellipsis overflow-hidden">#{item.name}</p>
            </li>
        )
    }
}

export default TendencyItem