import React, {Component} from 'react'

class LoadingItem extends Component{
    render(){
        return(
            <div className="w-100 bg-light position-relative rounded box-shadow post-item" style={{padding: '5px 0'}}>
                <div className="w-100 mb-3 d-flex flex-nowrap align-items-center">
                    <div className="mr-3 circle border bg-color-light-2"></div>
                    <div className="w-75 h-100 d-flex flex-column justify-content-around" style={{height: '40px'}}>
                        <div className="border rounded-pill bg-color-light-2" style={{width: '30%', height: '10px'}}></div>
                        <div className="border rounded-pill bg-color-light-2" style={{width: '40%', height: '10px'}}></div>
                    </div>
                </div>

                <div style={{height: '100px'}}></div>

                <div className="d-flex justify-content-around" >
                    <div className="border rounded-pill bg-color-light-2" style={{width: '30%', height: '10px'}}></div>
                    <div className="border rounded-pill bg-color-light-2" style={{width: '30%', height: '10px'}}></div>
                    <div className="border rounded-pill bg-color-light-2" style={{width: '30%', height: '10px'}}></div>
                </div>
            </div>
        )
    }
}

export default LoadingItem