import React, {Component} from 'react'
import classNames from 'classnames'

class Alert extends Component{
    render(){
        const { alertType, alertMessage } = this.props

        return(
            <div className="w-100 d-flex justify-content-center position-absolute top-10px" style={{zIndex: '9999'}}>
                <div className={classNames('alert', {'alert-warning': alertType === 'warning'}, {'alert-danger': alertType === 'danger'}, 'w-50')}>{alertMessage}</div>
            </div>      
        )
    }
}

export default Alert