import React, {Component} from 'react'

class Signup extends Component{
    constructor(){
        super()
        this.handleInfo = this.handleInfo.bind(this)
    }

    handleInfo(){
        const email = document.getElementById('email').value
        const password = document.getElementById('password').value
        const firstName = document.getElementById('firstName').value
        const lastName = document.getElementById('lastName').value
        const birthday = document.getElementById('day').value + '-' + document.getElementById('month').value + '-' + document.getElementById('year').value
        const gender = document.querySelector('input[name="gender"]:checked') ? document.querySelector('input[name="gender"]:checked').value : ''

        return {
            email, password, firstName, lastName, birthday, gender, avatar: 'https://www.gapo.vn/assets/images/default-user-avatar.png'
        }
    }

    render(){
        const {handleUserPage, handleSignup} = this.props

        return(
            <div className="card">
                <div className="mb-0 login-content">
                    <h1 className="mb-2"><b>Trở thành Gapoer?</b></h1>
                    <p className="text-secondary">Nhanh chóng và dễ dàng.</p>
                </div>
                <div className="border mb-3 mt-3"></div>
                <form onSubmit={async (event)=>{
                            event.preventDefault()
                            const userInfo = await this.handleInfo()
                            handleSignup(userInfo)
                        }}>
                    <div className="row mb-3">
                        <div className="col">
                            <input id="firstName" type="text" className="form-control bg-color-light-1" placeholder="Họ" />
                        </div>
                        <div className="col">
                            <input id="lastName" type="text" className="col form-control bg-color-light-1" placeholder="Tên" />
                        </div>
                    </div>
                    <div className="row mb-3">
                        <input id="email" type="email" className="form-control bg-color-light-1" placeholder="Email" />
                    </div>
                    <div className="row mb-3">
                        <input id="password" type="password" className="form-control bg-color-light-1" placeholder="Mật khẩu mới" />
                    </div>
                    <div className="mb-3">
                        <div className="row">
                            <p className="text-secondary">Ngày sinh</p>
                        </div>
                        <div className="row">
                            <div className="col">
                                <select id="day" className="form-control">
                                    <option defaultValue="">Ngày</option>
                                    <option value="1">1</option>
                                    <option value="2">2</option>
                                    <option value="3">3</option>
                                    <option value="4">4</option>
                                    <option value="5">5</option>
                                    <option value="6">6</option>
                                    <option value="7">7</option>
                                    <option value="8">8</option>
                                    <option value="9">9</option>
                                    <option value="10">10</option>
                                    <option value="11">11</option>
                                    <option value="12">12</option>
                                    <option value="13">14</option>
                                    <option value="14">14</option>
                                    <option value="15">15</option>
                                    <option value="16">16</option>
                                    <option value="17">17</option>
                                    <option value="18">18</option>
                                    <option value="19">19</option>
                                    <option value="20">20</option>
                                    <option value="21">21</option>
                                    <option value="22">22</option>
                                    <option value="23">23</option>
                                    <option value="24">24</option>
                                    <option value="25">25</option>
                                    <option value="26">26</option>
                                    <option value="27">27</option>
                                    <option value="28">28</option>
                                    <option value="29">29</option>
                                    <option value="30">30</option>
                                    <option value="31">31</option>
                                </select>
                            </div>
                            <div className="col">
                                <select id="month" className="form-control">
                                    <option defaultValue="">Tháng</option>
                                    <option value="JAN">Tháng 1</option>
                                    <option value="FEB">Tháng 2</option>
                                    <option value="MAR">Tháng 3</option>
                                    <option value="APR">Tháng 4</option>
                                    <option value="MAY">Tháng 5</option>
                                    <option value="JUN">Tháng 6</option>
                                    <option value="JUL">Tháng 7</option>
                                    <option value="AUG">Tháng 8</option>
                                    <option value="SEP">Tháng 9</option>
                                    <option value="OCT">Tháng 10</option>
                                    <option value="NOV">Tháng 11</option>
                                    <option value="DEC">Tháng 12</option>
                                </select>
                            </div>
                            <div className="col">
                                <select id="year" className="form-control">
                                    <option defaultValue="">Năm</option>
                                    <option value="2003">2003</option>
                                    <option value="2002">2002</option>
                                    <option value="2001">2001</option>
                                    <option value="2000">2000</option>
                                    <option value="1999">1999</option>
                                    <option value="1998">1998</option>
                                    <option value="1997">1997</option>
                                    <option value="1996">1996</option>
                                    <option value="1995">1995</option>
                                    <option value="1994">1994</option>
                                    <option value="1993">1993</option>
                                    <option value="1992">1992</option>
                                    <option value="1991">1991</option>
                                    <option value="1990">1990</option>
                                </select>
                            </div>
                        </div>
                    </div>
                    <div className="mb-3">
                        <div className="row">
                            <p className="text-secondary">Giới tính</p>
                        </div>
                        <div className="row">
                            <div className="col position-relative">
                                <label className="form-control d-flex align-items-center">Nam</label>
                                <input type="radio" name="gender" value="Male" className="position-absolute top-50 right-10px translate-y-50" />
                            </div>
                            <div className="col position-relative">
                                <label className="form-control d-flex align-items-center">Nữ</label>
                                <input type="radio" name="gender" value="Female" className="position-absolute top-50 right-10px translate-y-50" />
                            </div>
                            <div className="col position-relative">
                                <label className="form-control d-flex align-items-center">Tùy chọn</label>
                                <input type="radio" name="gender" value="Option" className="position-absolute top-50 right-10px translate-y-50" />
                            </div>
                        </div>
                    </div>
                    <div className="row mb-3">
                        <p className="text-secondary text-wrap">
                            Bằng cách nhấp vào Đăng ký, 
                            bạn đồng ý với <a>Điều khoản</a>, 
                            <a> Chính sách dữ liệu </a>
                            và <a>Chính sách cookie</a> của chúng tôi. Bạn có thể nhận được thông báo của chúng tôi qua SMS và hủy nhận bất kỳ lúc nào.
                        </p>
                    </div>
                    <div className="row mb-3">
                        <button className="form-control bg-success cursor-pointer">Đăng ký</button>
                    </div>
                    <div className="border mb-3 mt-3"></div>
                    <div className="row mb-3">
                        <input onClick={()=>{handleUserPage(true)}} type="button" className="form-control bg-primary cursor-pointer" value="Đăng nhập" />
                    </div>  
                </form>
            </div>
        )
    }
}

export default Signup