import React, {Component} from 'react'
import ResultItem from './ResultItem/Index'

class ResultBox extends Component{
    constructor(){
        super()
        this.state = {
            searchResults: [],
            isResult: false
        }
        this.getUsers = this.getUsers.bind(this)
    }

    componentDidMount(){
        this.getUsers()
    }

    componentDidUpdate(preProps){
        const {searchValue} = this.props

        if(searchValue !== preProps.searchValue){
            this.getUsers()
        }
    }

    getUsers(){
        const { searchValue, _id } = this.props

        this.setState({
            searchResults: []
        })

        fetch('http://localhost:3001/users')
        .then(res => res.json())
        .then(data => {
            if(data.status === 'success'){
                if(searchValue){
                    data.doc.map(user => {
                        if(user._id !== _id){
                            if(user.firstName.toLowerCase().includes(searchValue.toLowerCase()) || user.lastName.toLowerCase().includes(searchValue.toLowerCase())){
                                this.setState({
                                    searchResults: [...this.state.searchResults, user],
                                    isResult: true
                                })
                            }
                        }
                    })
                } else{
                    this.setState({
                        isResult: false
                    })
                }
            }
        })
    }

    render(){
        const { _id, isUpdateFriend } = this.props

        return(
            <div className="card position-absolute top-100 bg-light text-wrap rounded border z-index-99 transition-03 search--results-box">
                <p className="p-3 fw-6">Kết quả tìm kiếm</p>
                <div className="mt-3 border"></div>
                <div className="p-3 search--results">
                    {this.state.isResult ? this.state.searchResults.map(user => <ResultItem key={user._id} _id={_id} user={user} isUpdateFriend={isUpdateFriend} />) : 'Không tìm thấy kết quả'}
                </div>
            </div>
        )
    }
}

export default ResultBox