import React, {Component} from 'react'
import UserPage from './components/UserPage/Index'
import HomePage from './components/HomePage/Index'
import './libs/m-style/Style.css'

class App extends Component{
  constructor(){
    super()
    this.state = {
      isLoggin: false,
      _id: ''
    }

    this.handleLogin = this.handleLogin.bind(this)
  }

  handleLogin(boolean, _id){
    this.setState({
      isLoggin: boolean,
      _id
    })
  }

  render(){
    return(
      <div>
        {this.state.isLoggin ? <HomePage _id={this.state._id} handleLogin={this.handleLogin} /> : <UserPage  handleLogin={this.handleLogin}/>}
      </div>
    )
  }
}

export default App;
