import React, {Component} from 'react'

class GroupSelected extends Component{
    render(){
        const {groupInfo} = this.props
        
        return(
            <div className="mt-3">
                <div className="p-3 d-flex align-items-center rounded bg-color-light-1 transition-03 cursor-pointer">
                    <img className="mr-2" width="30px" src={groupInfo.avatar} alt="" />
                    {groupInfo.name}
                </div>
            </div>
        )
    }
}

export default GroupSelected