import React, {Component} from 'react'

class ShowGroupsBtn extends Component{
    render(){
        const {handleGroupsList} = this.props

        return(
            <div onClick={()=>{handleGroupsList()}} className="py-3 col d-flex justify-content-center rounded cursor-pointer hover-gray">
                <div className="d-flex justify-content-center align-items-center">
                    <img className="mr-2" width="30px" src="https://www.gapo.vn/assets/icons/group-active.svg" alt="" />
                    Nhóm
                </div>
            </div>
        )
    }
}

export default ShowGroupsBtn