import React, {Component} from 'react'
import GetUserByID from '../../../GetUserByID'

import Friend from './Friend/Index'
import FriendRequests from './FriendRequests/Index'

class FriendsContact extends Component{
    constructor(){
        super()
        this.state = {
            friends: [],
            friendRequests: [],
            isLoadFriends: false,
        }
        this.handleFriendsList = this.handleFriendsList.bind(this)
    }

    UNSAFE_componentWillMount(){
        this.handleFriendsList()
    }

    UNSAFE_componentWillReceiveProps(prevProp){
        if(prevProp.isUpdateFriend !== this.props.isUpdateFriend){
            this.handleFriendsList()
        }
    }

    async handleFriendsList(){
        const {userInfo} = this.props

        this.setState({
            friends: [],
            friendRequests: [],
            isLoadFriends: false,
        })

        const data = await GetUserByID(userInfo._id)
        if(data.status === 'success'){
            this.setState({
                friends: []
            })

            data.doc.friends.map(async (friend) => {
                if(friend.status === true){
                    const data = await GetUserByID(friend._id)
                    if(data.status === 'success'){
                        this.setState({
                            friends: [...this.state.friends, data.doc],
                        })
                    }
                }

                if(friend.status === 'responding'){
                    const data = await GetUserByID(friend._id)
                    if(data.status === 'success'){
                        this.setState({
                            friendRequests: [...this.state.friendRequests, data.doc],
                        })
                    }
                }
            })
        }

        this.setState({
            isLoadFriends: true,
        })
    }

    render(){
        const {handleFriendStatus, userInfo, handleFriendMessage} = this.props

        return(
            <aside className="col-lg-3 col-md-4 col-0 bg-light position-fixed main-side--left">
                <div className="p-3 row flex-column">
                    <div className="row mt-2 p-2 align-items-center">
                        <span className='pr-2'><h3>Tài khoản: 0</h3></span>
                        <span>
                            <img src="https://www.gapo.vn/assets/icons/payment/Gcoin.svg" alt="" />
                        </span>
                        <span className="ml-auto mr-3 p-3 rounded bg-color-light-2 fw-7 text-secondary cursor-pointer">Mua Coin</span>
                    </div>

                    <div className="m-3 border"></div>

                    {this.state.friendRequests.length > 0 ? <div className="p-3 fw-6 text-secondary">Lời mời kết bạn</div> : ''}
                    {this.state.friendRequests.length > 0 ? 
                        <div className="text-secondary">
                            {this.state.friendRequests.map(friend => <FriendRequests key={friend._id} friendInfo={friend} userInfo={userInfo} handleFriendStatus={handleFriendStatus} />)}
                        </div>
                    : ''}
                    
                    {this.state.friendRequests.length > 0 ? <div className="m-3 border"></div> : ''}

                    <div className="p-2 fw-6 text-secondary friend-contact">Người liên hệ</div>
                    <div className="users-list text-secondary">
                        {this.state.isLoadFriends ? this.state.friends.map(friend => <Friend key={friend._id} userInfo={friend} handleFriendMessage={handleFriendMessage}/>) : ''}
                    </div>
                </div>
            </aside>
        )
    }
}

export default FriendsContact