import React, {Component} from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faSearch } from '@fortawesome/free-solid-svg-icons'
import ResultBox from './ResultsBox/Index'

class SearchBar extends Component{
    constructor(){
        super()
        this.state = {
            isShowResultBox: false,
            searchValue: ''
        }

        this.handleTimeOut = 0
        this.handleSearch = this.handleSearch.bind(this)
    }

    handleSearch(event){
        if(this.handleTimeOut){
            clearTimeout(this.handleTimeOut)
        }

        if(!event.target.value){
            this.handleTimeOut = setTimeout(()=>{
                this.setState({
                    isShowResultBox: false,
                    searchValue: event.target.value
                })
            }, 300)

            return
        }
        
        this.handleTimeOut = setTimeout(()=>{
            this.setState({
                isShowResultBox: true,
                searchValue: event.target.value
            })
        }, 300)
    }

    render(){
        const {_id, isUpdateFriend} = this.props

        return(
            <div className="h-100 position-relative d-flex align-items-center">
                <input type="search" className="form-control rounded-pill header-search" placeholder="Tìm kiếm" onChange={this.handleSearch}/>
                <FontAwesomeIcon icon={faSearch} className="position-absolute text-secondary top-50 left-20px translate-50 cursor-pointer" />
                {this.state.isShowResultBox ? <ResultBox _id={_id} searchValue={this.state.searchValue} isUpdateFriend={isUpdateFriend} /> : ''}
            </div>
        )
    }
}

export default SearchBar