import React, {Component} from 'react'
import GetGroups from '../../../../../GetGroups'
import Group from './Group/Index'

class GroupsList extends Component{
    constructor(){
        super()
        this.state = {
            groupsList: [],
            isLoaded: false
        }
    }

    async componentDidMount(){
        const groups = await GetGroups()
        if(groups.status === 'success'){
            this.setState({
                groupsList: groups.doc,
            })
        }
    }

    componentDidUpdate(prevProps, prevState){
        if(prevState.groupsList.length !== this.state.groupsList.length){
            this.setState({
                isLoaded: true,
            })
        }
    }

    render(){
        const {handleSelectedGroup} = this.props

        return(
            <div className="mt-3 form-control overflow-hidden overflow-y-scroll" style={{maxHeight: '200px'}}>
                <div onClick={()=>{handleSelectedGroup()}} className="p-3 d-flex align-items-center rounded hover-gray transition-03 cursor-pointer group-list--item">
                    -- Nhóm --
                </div>

                {this.state.isLoaded ? this.state.groupsList.map(group => <Group key={group._id} groupInfo={group} handleSelectedGroup={handleSelectedGroup} />) : ''}
            </div>
        )
    }
}

export default GroupsList