import React, {Component} from 'react'
import GetUserByID from '../GetUserByID'
import Alert from '../Alert'

import Header from './Header/Index'
import Main from './Main/Index'

class HomePage extends Component{
    constructor(){
        super()
        this.state = {
            userInfo: {},
            menuSelected: 'home',
            
            isLoading: false,
            isUpdateFriend: false,

            isAlert: false,
            alertType: '',
            alertMessage: ''
        }

        this.isUpdateFriend = this.isUpdateFriend.bind(this)
        this.handleMenuSelected = this.handleMenuSelected.bind(this)
        this.handleAlert = this.handleAlert.bind(this)
    }

    UNSAFE_componentWillMount(){
        const {_id} = this.props

        GetUserByID(_id)
        .then(data => {
            if(data.status === 'success'){
                this.setState({
                    userInfo: data.doc,
                    isLoading: true
                })
            }
        })
    }

    handleAlert(alertType, alertMessage, status){
        this.setState({
            alertType,
            alertMessage,
            isAlert: status
        })
    }

    isUpdateFriend(status){
        this.setState({
            isUpdateFriend: status
        })
    }

    handleMenuSelected(type){
        this.setState({
            menuSelected: type
        })
    }

    render(){
        const {handleLogin} = this.props

        return(
            <div>
                {this.state.isAlert ? <Alert alertType={this.state.alertType} alertMessage={this.state.alertMessage} /> : ''}
                {this.state.isLoading ? <Header userInfo={this.state.userInfo} handleLogin={handleLogin} isUpdateFriend={this.isUpdateFriend} handleMenuSelected={this.handleMenuSelected} menuSelected={this.state.menuSelected} /> : ''}
                {this.state.isLoading ? <Main userInfo={this.state.userInfo} isUpdateFriend={this.state.isUpdateFriend} handleFriendStatus={this.isUpdateFriend} menuSelected={this.state.menuSelected} handleAlert={this.handleAlert}/> : ''}
            </div>
        )
    }
}

export default HomePage