export default function HandleTime(timestamps) {
    const now = (new Date()).getTime()
    const secondsAgo = Math.floor((now - timestamps) / 1000)
    const minutesAgo = Math.floor((now - timestamps) / 1000 / 60)
    const hoursAgo = Math.floor((now - timestamps) / 1000 / 60 / 60)
    const daysAgo = Math.floor((now - timestamps) / 1000 / 60 / 60 / 24)
    const weeksAgo = Math.floor((now - timestamps) / 1000 / 60 / 60 / 24 / 7)

    if(weeksAgo >= 1){
        return `${weeksAgo} tuần`
    }

    if(daysAgo >= 1){
        return `${daysAgo} ngày`
    }

    if(hoursAgo >= 1){
        return `${hoursAgo} giờ`
    }

    if(minutesAgo >= 1 ){
        return `${minutesAgo} phút`
    }

    if(secondsAgo >= 1){
        return `${secondsAgo} giây trước`
    }

    return 'Vừa xong'
}