import React, {Component} from 'react'

class Home extends Component{
    constructor(props){
        super(props)
        this.state = {
            menuSelected: props.menuSelected
        }
    }

    componentDidUpdate(prevProps){
        if(prevProps.menuSelected !== this.props.menuSelected){
            this.setState({
                menuSelected: this.props.menuSelected
            })
        }
    }

    render(){
        return(
            <span className="mb-5 d-flex justify-content-center">
                {this.state.menuSelected === 'home' ? 
                    <img className="header-menu-type" src="https://www.gapo.vn/assets/icons/home-active.svg" alt="" /> :
                    <img className="header-menu-type" src="https://www.gapo.vn/assets/icons/home-default.svg" />
                }
            </span>
        )
    }
}

export default Home