import React, { Component } from "react";
import GetGroups from '../../../../../GetGroups'

import Group from './Group/Index'

class Groups extends Component{
    constructor(){
        super()

        this.state = {
            groups: [],
            isLoaded: false
        }
    }

    async UNSAFE_componentWillMount(){
        this.setState({
            isLoaded: false
        })

        const groups = await GetGroups()
        if(groups.status === 'success'){
            this.setState({
                groups: groups.doc,
                isLoaded: true
            })
        }
    }

    async UNSAFE_componentWillReceiveProps(){
        this.setState({
            isLoaded: false
        })
        
        const groups = await GetGroups()
        if(groups.status === 'success'){
            this.setState({
                groups: groups.doc,
                isLoaded: true
            })
        }
    }

    render(){
        const {handleShowGroupBox, handleGroupSelected} = this.props

        return(
            <div>
                <h3 className="mt-3 p-3">Nhóm</h3>
                <div className="m-3 p-3 d-flex align-items-center rounded hover-gray bg-color-green-1 text-success fw-6 transition-03 cursor-pointer">
                    <img className="mr-3" src="https://www.gapo.vn/assets/icons/Frame%20988.svg" alt="" />
                    Bảng tin của bạn
                </div>
                <div className="m-3 p-3 d-flex align-items-center rounded hover-gray bg-white text-secondary fw-6 transition-03 cursor-pointer">
                    <img className="mr-3" src="https://www.gapo.vn/assets/icons/Frame%20988%20(1).svg" alt="" />
                    Khám phá
                </div>
                <div onClick={()=>handleShowGroupBox(true)} className="m-3 p-3 d-flex justify-content-center align-items-center rounded bg-white text-secondary transition-03 cursor-pointer create-group--btn">
                    <img className="mr-3" src="https://www.gapo.vn/assets/icons/plus-create-make.svg" alt="" />
                    Tạo nhóm mới
                </div>

                <div className="my-5 mx-3 border"></div>

                <div className="my-3 pt-0 pb-3 px-3">
                    <p className="my-3 fw-6">Nhóm bạn tham gia</p>

                    <ul className="py-3 d-flex flex-column group-items">
                        <li onClick={()=>{handleGroupSelected('')}} className="p-3 d-flex align-items-center rounded cursor-pointer group-item hover-gray transition-03">
                            <img className="mr-3 rounded" src="https://cdn-thumb-image-5.gapo.vn/56x56/smart/502726b8-3f51-41cc-9511-32c5d3b650d2.jpeg" alt="" />
                            Box Girl ❤
                        </li>

                        {this.state.isLoaded ? this.state.groups.map(groupInfo => <Group key={groupInfo._id} groupInfo={groupInfo} handleGroupSelected={handleGroupSelected}/>) : ''}
                    </ul>
                </div>
            </div>
        )
    }
}

export default Groups