import React, {Component} from 'react'

class Group extends Component{
    render(){
        const {groupInfo, handleSelectedGroup} = this.props

        return(
            <div onClick={()=>{handleSelectedGroup(groupInfo._id)}} className="p-3 rounded hover-gray transition-03 cursor-pointer">
                <div className="d-flex align-items-center">
                    <img className="mr-2" width="30px" src={groupInfo.avatar} alt="" />
                    {groupInfo.name}
                </div>
            </div>
        )
    }
}

export default Group