import React, {Component} from 'react'
import { faChevronLeft, faChevronRight } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

import TendencyItem from './TendencyItem/Index'

class Tendency extends Component{
    constructor(){
        super()

        this.handleSlideTendency = this.handleSlideTendency.bind(this)
        this.tendencyItems = [
            {
                name: 'phim',
                img: 'https://image-1.gapo.vn/images/a692a630-3a31-46ea-8002-9bc5ef78c964.jpeg'
            },
            {
                name: 'vongquaymayman',
                img: 'https://image-5.gapo.vn/images/40c90734-b042-4abe-95f9-421f142919fc.jpeg'
            },
            {
                name: 'tamsu',
                img: 'https://image-1.gapo.vn/images/0f5f9636-d042-439d-9efc-dfe55b112154.jpeg'
            },
            {
                name: 'chiase',
                img: 'https://image-5.gapo.vn/images/91ef2171-c618-4eba-8f93-4a7f3dc99772.jpeg'
            },
            {
                name: 'muaban',
                img: 'https://image-5.gapo.vn/images/a7c2f462-ac7e-4887-bc01-5dcd7d9243ff.jpeg'
            },
        ]
    }

    handleSlideTendency(type){
        if(this.handleTimeOut){
            clearTimeout(this.handleTimeOut)
        }

        this.handleTimeOut = setTimeout(()=>{
            const slide = document.getElementById('post-tendency-slide')
            const marginLeft = +(window.getComputedStyle(slide).marginLeft.split('px').join(''))
            const left = document.getElementById('post-tendency-slide--left')
            const right = document.getElementById('post-tendency-slide--right')
            const tendencyItems = document.getElementsByClassName('post-tendency-item')

            if(type === 'left'){
                if(-marginLeft <= 0){
                    left.style.pointerEvents = 'none'
                    return
                }
                right.style.pointerEvents = 'unset'
                slide.style.marginLeft = `${marginLeft + 158}px`
            }

            if(type === 'right'){
                if(marginLeft <= -(tendencyItems.length * 158 - 158 * 2 - 8)){
                    right.style.pointerEvents = 'none'
                    return
                }

                left.style.pointerEvents = 'unset'
                slide.style.marginLeft = `${marginLeft - 158}px`
            }
        }, 300)
    }

    render(){
        return(
            <div className="card-min-width col-8 p-2 d-flex flex-column align-items-center bg-light rounded box-shadow">
                <div className="row mb-3 align-items-center fw-6 fs-01">
                    <img className="mr-3" src="https://www.gapo.vn/assets/images/icon-trend.svg" alt="" />
                    Xu hướng nổi bậc
                </div>

                <div className="row position-relative overflow-hidden">
                    <ul id="post-tendency-slide" className="row post-tendency-slide">
                        {this.tendencyItems.map((item, index) => <TendencyItem key={index} item={item} />)}

                        <div 
                            onClick={()=>{
                                this.handleSlideTendency('left')
                            }}
                            id="post-tendency-slide--left"
                            className="ml-1 circle position-absolute top-50 left-0 translate-y-50 bg-dark text-center text-light post-tendency-slide--left">
                            <FontAwesomeIcon icon={faChevronLeft} />
                        </div>
                        <div
                            onClick={()=>{
                                this.handleSlideTendency('right')
                            }}
                            id="post-tendency-slide--right"
                            className="mr-1 circle position-absolute top-50 right-0 translate-y-50 bg-dark text-center text-light post-tendency-slide--right">
                            <FontAwesomeIcon icon={faChevronRight} />
                        </div>
                    </ul>
                </div>
            </div>
        )
    }
}

export default Tendency