import axios from "axios";
import React, { Component } from "react";
import { URL } from "../../../../../constants/url";

class GroupBox extends Component {
  createGroup() {
    const name = document.getElementById("name").value;
    const description = document.getElementById("description").value;

    if (!name) {
      this.props.handleAlert("warning", "Chưa nhập tên nhóm.", true);
      return;
    }

    axios
      .post(URL + "/groups", {
        name,
        description,
        avatar: "https://www.gapo.vn/assets/icons/group-active.svg",
      })
      .then((res) => res.data)
      .then((data) => {
        if (data.status === "success") {
          this.props.isCreateGroup();
        }
      });

    this.props.handleAlert("", "", false);
  }

  render() {
    return (
      <div
        className="card-min-width p-2 d-flex flex-column position-absolute bg-light rounded box-shadow z-index-9"
        style={{ maxWidth: "500px" }}
      >
        <div>
          <h1 className="mb-2 text-secondary">Tạo nhóm mới</h1>
          <p className="fs-08 text-secondary">
            Tạo không gian tuyệt vời để hoàn thành công việc và chỉ liên lạc với
            những người bạn muốn. Hãy chia sẻ ảnh và video, trò chuyện, lên kế
            hoạch và nhiều hoạt động khác với những người bạn trên Gapo.
          </p>
        </div>

        <div className="my-3 border"></div>

        <div className="mb-3">
          <p className="mb-1">Tên nhóm</p>
          <input
            id="name"
            className="w-100 form-control"
            type="text"
            placeholder="Nhập tên nhóm..."
          />
        </div>

        <div>
          <p className="mb-1">Mô tả</p>
          <textarea
            id="description"
            className="w-100 form-control fs-15 border-0"
            cols="50"
            rows="7"
            placeholder="Mô tả..."
          ></textarea>
        </div>

        <div className="my-3 border"></div>

        <div>
          <button
            onClick={() => {
              this.createGroup();
            }}
            className="w-100 p-3 bg-color-green-2 fs-1 text-light rounded outline-none border-0 cursor-pointer transition-03"
          >
            Tạo nhóm
          </button>
        </div>
      </div>
    );
  }
}

export default GroupBox;
