import React, { Component } from "react";

class Home extends Component{
    render(){
        return(
            <div>
                <h3 className="mt-3 p-3">Trang chủ</h3>
                <ul className="p-3 d-flex flex-column">
                    <li className="p-3 d-flex align-items-center rounded hover-gray bg-color-light-2 transition-03 cursor-pointer">
                        <img className="mr-3" src="http://gapo.vn/assets/images/svg-icons/explore.svg" alt="" />
                        Khám phá
                    </li>
                    <li className="p-3 d-flex align-items-center rounded hover-gray hover-gray:hover transition-03 cursor-pointer">
                        <img className="mr-3" src="http://gapo.vn/assets/images/svg-icons/follow-feed.svg" alt="" />
                        Theo dõi
                    </li>
                    <li className="p-3 d-flex align-items-center rounded hover-gray hover-gray:hover transition-03 cursor-pointer">
                        <img className="mr-3" src="http://gapo.vn/assets/images/svg-icons/search-colorful.svg" alt="" />
                        Tìm bạn bè
                    </li>
                    <li className="p-3 d-flex align-items-center rounded hover-gray hover-gray:hover transition-03 cursor-pointer">
                        <img className="mr-3" src="http://gapo.vn/assets/images/svg-icons/flag-page.svg" alt="" />
                        Trang của bạn
                    </li>
                </ul>
            </div>
        )
    }
}

export default Home