import React, {Component} from 'react'
import FriendStatus_Responding from '../../../Header/SearchBar/ResultsBox/ResultItem/FriendStatus/Responding/Index'

class FriendRequests extends Component{
    render(){
        const {userInfo, friendInfo, handleFriendStatus} = this.props

        return(
            <div className="mb-1 p-2 d-flex align-items-center rounded hover-gray cursor-pointer user--info">
                <img className="circle mr-3 border" src={friendInfo.avatar} alt="" />
                <span className="fw-6">{friendInfo.firstName} {friendInfo.lastName}</span>
                <FriendStatus_Responding friendID={friendInfo._id} userID={userInfo._id} handleFriendStatus={handleFriendStatus} />
            </div>
        )
    }
}

export default FriendRequests