import React, {Component} from 'react'

class PostButton extends Component{
    render(){
        const {handlePostBox, userAvatar} = this.props

        return(
            <div className="card-min-width col-8 p-2 d-flex flex-nowrap align-items-center bg-light rounded box-shadow">
                <img className="mr-auto circle border" src={userAvatar} alt="" />
                <button onClick={()=>{handlePostBox(true)}} className="mx-3 form-control hover-light-green cursor-pointer post-show-box">Đăng bài viết</button>
                <div className="ml-auto circle hover-gray d-flex justify-content-center align-items-center bg-light post-show-box">
                    <img onClick={()=>{handlePostBox(true)}} width="50%" src="https://www.gapo.vn/assets/images/svg-icons/picture-video.svg" alt="" />
                </div>
            </div>
        )
    }
}

export default PostButton