import React, {Component} from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faGlobeAsia, faCommentAlt, faShareSquare } from '@fortawesome/free-solid-svg-icons'
import classNames from 'classnames'
import HandleTime from '../../../../HandleTime'
import GetUserByID from '../../../../GetUserByID'
import GetPostByID from '../../../../GetPostByID'
import UpdatePostByID from '../../../../UpdatePostByID'

import PostComments from './PostComments/Index'
import PostReaction from './PostReaction/Index'
import Like from './Reaction/Like'
import Love from './Reaction/Love'
import Haha from './Reaction/Haha'
import Wow from './Reaction/Wow'
import Sad from './Reaction/Sad'
import Angry from './Reaction/Angry'

class PostItem extends Component{
    constructor(){
        super()
        this.state = {
            countReaction: 0,
            countComments: 0,
            isReaction: false,

            author: {},
            post: {},

            reactionSelected: ''
        }
        this.timeOut = 0

        this.countReaction = this.countReaction.bind(this)
        this.countComments = this.countComments.bind(this)
        this.getAuthorInfo = this.getAuthorInfo.bind(this)
        this.checkUserReaction = this.checkUserReaction.bind(this)
        this.handleUpdateReaction = this.handleUpdateReaction.bind(this)
    }

    async UNSAFE_componentWillMount(){
        await this.getPostByID()
        this.getAuthorInfo()
        this.countComments()
        this.countReaction()
        this.checkUserReaction()
    }

    async componentDidUpdate(prevProps, prevState){
        if(prevState.post !== this.state.post){
            this.countComments()
            this.countReaction()
        }
    }

    countReaction(){
        const {post} = this.state
        const countReaction = post.reaction.like.length + post.reaction.haha.length + post.reaction.love.length + post.reaction.wow.length + post.reaction.sad.length + post.reaction.angry.length

        this.setState({
            countReaction
        })
    }

    countComments(){
        const {post} = this.state
        const countComments = post.comments.length

        this.setState({
            countComments
        })
    }

    checkUserReaction(){
        const {userInfo} = this.props
        const {post} = this.state

        if(post.reaction.like.indexOf(userInfo._id) !== -1){
            this.setState({
                reactionSelected: 'like',
                isReaction: true
            })

            return
        }

        if(post.reaction.love.indexOf(userInfo._id) !== -1){
            this.setState({
                reactionSelected: 'love',
                isReaction: true
            })

            return
        }

        if(post.reaction.haha.indexOf(userInfo._id) !== -1){
            this.setState({
                reactionSelected: 'haha',
                isReaction: true
            })

            return
        }

        if(post.reaction.wow.indexOf(userInfo._id) !== -1){
            this.setState({
                reactionSelected: 'wow',
                isReaction: true
            })

            return
        }

        if(post.reaction.sad.indexOf(userInfo._id) !== -1){
            this.setState({
                reactionSelected: 'sad',
                isReaction: true
            })

            return
        }

        if(post.reaction.angry.indexOf(userInfo._id) !== -1){
            this.setState({
                reactionSelected: 'angry',
                isReaction: true
            })

            return
        }
    }

    handleReactionBox(status){
        const reactionBox = document.getElementById(this.props.id)

        if(status === true){
            reactionBox.style.transform = 'translate(0, -100%)'
            reactionBox.style.visibility = 'visible'
        }

        if(status === false){
            reactionBox.style.transform = 'translate(0, -75%)'
            reactionBox.style.visibility = 'hidden'
        }
    }

    async getPostByID(){
        const {id} = this.props

        const post = await GetPostByID(id)
        if(post.status === 'success'){
            this.setState({
                post: post.doc
            })
        }
    }

    async getAuthorInfo(){
        const {post} = this.state

        const author = await GetUserByID(post.userID)
        if(author.status === 'success'){
            this.setState({
                author: author.doc
            })
        }
    }

    handleUpdateReaction(type){
        this.handleReactionBox(false)

        const {id, userInfo} = this.props
        const status = type ? true : false

        if(this.timeOut) clearTimeout(this.timeOut)

        this.timeOut = setTimeout(async()=>{
            const post = await UpdatePostByID(id, 'reaction', {
                userID: userInfo._id,
                type,
                status
            })

            if(post.status === 'success'){
                this.setState({
                    post: post.doc
                })
            }

            this.setState({
                reactionSelected: type,
                isReaction: true
            })
    
            if(!type){
                this.setState({
                    isReaction: false
                })
            }
        }, 300)
    }

    render(){
        const {id, userInfo} = this.props
        const {post} = this.state
        const author = this.state.author

        return(
            <div className="w-100 bg-light position-relative rounded box-shadow post-item" style={{padding: '5px 0'}}>
                <div className="mb-3 d-flex flex-nowrap align-items-center">
                    <img className="mr-3 circle border" src={author.avatar} alt="" />
                    <div>
                        <div className="d-inline-block fw-6 mb-2">{author.firstName} {author.lastName}</div>
                        <div className="fs-08 text-secondary text-nowrap">
                            <span className="mt-3 pr-5 position-relative post-date">
                                {HandleTime(post.timestamps)}
                            </span>
                            <span className="pl-5" style={{marginLeft: '5px'}}><FontAwesomeIcon icon={faGlobeAsia}/></span>
                        </div>
                    </div>
                </div>

                <div>
                    {post.content}
                </div>
                
                <div className={classNames('d-flex', 'text-secondary', {'m-0': !this.state.countReaction & !this.state.countComments})}>
                    {this.state.countReaction > 0 ? <PostReaction post={post} /> : ''}
                    {this.state.countComments > 0 ? <PostComments post={post} /> : ''}
                </div>

                <div className="m-0 position-relative border">
                    <div id={id} 
                        onMouseEnter={()=>{this.handleReactionBox(true)}}
                        onMouseLeave={()=>{this.handleReactionBox(false)}}
                        className="d-flex p-2 position-absolute rounded-pill border bg-light reaction-box">
                        
                        <Like postID={id} userID={userInfo._id} handleUpdateReaction={this.handleUpdateReaction}/>
                        <Love postID={id} userID={userInfo._id} handleUpdateReaction={this.handleUpdateReaction}/>
                        <Haha postID={id} userID={userInfo._id} handleUpdateReaction={this.handleUpdateReaction}/>
                        <Wow postID={id} userID={userInfo._id} handleUpdateReaction={this.handleUpdateReaction}/>
                        <Sad postID={id} userID={userInfo._id} handleUpdateReaction={this.handleUpdateReaction}/>
                        <Angry postID={id} userID={userInfo._id} handleUpdateReaction={this.handleUpdateReaction}/>
                    </div>
                </div>

                <div className="row m-1 p-0 text-secondary">
                        {this.state.isReaction ? 
                        <div 
                            onMouseEnter={()=>{this.handleReactionBox(true)}}
                            onMouseLeave={()=>{this.handleReactionBox(false)}}
                            onClick={()=>{
                                this.handleUpdateReaction('')
                            }}
                            className="p-2 col d-flex justify-content-center align-items-center fw-06 cursor-pointer rounded hover-gray reaction-show-box" style={{color: "#6fbe44"}}>
                            {
                                this.state.reactionSelected === 'like' ? <Like postID={id} userID={userInfo._id} handleUpdateReaction={this.handleUpdateReaction} content="Like"/> : 
                                this.state.reactionSelected === 'love' ? <Love postID={id} userID={userInfo._id} handleUpdateReaction={this.handleUpdateReaction} content="Love"/> :
                                this.state.reactionSelected === 'haha' ? <Haha postID={id} userID={userInfo._id} handleUpdateReaction={this.handleUpdateReaction} content="Haha"/> :
                                this.state.reactionSelected === 'wow' ? <Wow postID={id} userID={userInfo._id} handleUpdateReaction={this.handleUpdateReaction} content="Wow"/> :
                                this.state.reactionSelected === 'sad' ? <Sad postID={id} userID={userInfo._id} handleUpdateReaction={this.handleUpdateReaction} content="Sad"/> :
                                this.state.reactionSelected === 'angry' ? <Angry postID={id} userID={userInfo._id} handleUpdateReaction={this.handleUpdateReaction} content="Angry"/> : ''
                            }
                        </div> :

                        <div 
                            onMouseEnter={()=>{this.handleReactionBox(true)}}
                            onMouseLeave={()=>{this.handleReactionBox(false)}}
                            onClick={()=>{this.handleUpdateReaction('')}}
                            className="p-2 col d-flex justify-content-center align-items-center fw-06 cursor-pointer rounded hover-gray reaction-show-box">
                            
                            <img className="mr-3 circle-auto" src="http://gapo.vn/assets/images/png-icons/like-reaction.png" alt="" />
                            Thích
                        </div>}

                    <div className="p-2 col d-flex justify-content-center align-items-center fw-06 cursor-pointer rounded hover-gray">
                        <FontAwesomeIcon icon={faCommentAlt} className="mr-3" />Bình luận
                    </div>
                    <div className="p-2 col d-flex justify-content-center align-items-center fw-06 cursor-pointer rounded hover-gray">
                        <FontAwesomeIcon icon={faShareSquare} className="mr-3" />Chia sẻ
                    </div>
                </div>

                <div className="mx-0 mt-0 border"></div>

                <div className="mt-0 pt-0 post-item-comments"></div>

                <div className="my-0 mb-2 d-flex">
                    <img className="mr-auto circle border" src={userInfo.avatar} alt="" />
                    <input className="mx-2 form-control bg-color-light-1 rounded-pill post-item-comment-content" type="text" placeholder="Viết bình luận..." />
                    <img className="ml-auto circle post-item-comment-send" src="https://www.gapo.vn/assets/images/svg-icons/send-message-icon.svg" alt="" />
                </div>
            </div>
        )
    } 

}

export default PostItem