import React, { Component } from "react";
import { faTimes } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import classNames from "classnames";

import NoMessageInfo from "./NoMessageInfo/Index";

class Messages extends Component {
  render() {
    const { messagesInfo, handleShowMessageBox } = this.props;

    return (
      <div
        className="mr-5 position-fixed bottom-0 right-0 bg-light box-shadow rounded messages-box"
        style={{ height: "450px", width: "330px" }}
      >
        <div
          className="px-3 d-flex align-items-center border bg-color-green-2 text-light rounded-top"
          style={{ height: "50px" }}
        >
          <img
            className="mr-2 circle circle-sm border"
            src={messagesInfo.friendInfo.avatar}
          />
          <div className="cursor-pointer">
            {messagesInfo.friendInfo.firstName}{" "}
            {messagesInfo.friendInfo.lastName}
          </div>
          <FontAwesomeIcon
            onClick={() => {
              handleShowMessageBox(false);
            }}
            icon={faTimes}
            className="ml-auto fs-15 cursor-pointer"
          />
        </div>

        <div
          className="overflow-y-scroll friend-message-items--wrapped"
          style={{ height: "calc(100% - 100px)" }}
        >
          <div
            className={classNames({
              "d-flex": messagesInfo.messages.length !== 0,
              "flex-column-reverse": messagesInfo.messages.length !== 0,
              "pt-5": messagesInfo.messages.length === 0,
            })}
            style={{ minHeight: "100%" }}
          >
            {messagesInfo.messages.length !== 0 ? (
              "run"
            ) : (
              <NoMessageInfo friendInfo={messagesInfo.friendInfo} />
            )}
            {/* <div className="mr-3 my-3 ml-auto px-3 py-2 bg-color-green-2 text-light text-wrap rounded" style={{maxWidth: '70%'}}>
                            Hello
                        </div>
                        <div className="ml-3 my-3 mr-auto px-3 py-2 bg-color-light-2 text-wrap rounded" style={{maxWidth: '70%'}}>
                            Bye
                        </div> */}
          </div>
        </div>

        <div
          className="mt-auto px-2 d-flex align-items-center border-top"
          style={{ height: "50px" }}
        >
          <img
            className="mr-3 cursor-pointer"
            src="https://www.gapo.vn/assets/icons/gif-icon.svg"
            alt=""
          />
          <img
            className="mr-3 cursor-pointer"
            src="https://www.gapo.vn/assets/images/chat/image.svg"
            alt=""
          />
          <img
            className="mr-3 cursor-pointer"
            src="https://www.gapo.vn/assets/images/chat/sticker.svg"
            alt=""
          />
          <div>
            <input
              id="message-content"
              className="form-control bg-color-light-1 rounded-pill"
              type="text"
              placeholder="Aa"
              style={{ width: "190px", padding: ".5em .7em", fontSize: ".9em" }}
            />
          </div>
          <img
            id="message-send"
            className="cursor-pointer"
            width="30px"
            src="https://www.gapo.vn/assets/images/svg-icons/send-message-icon.svg"
            alt=""
          />
        </div>
      </div>
    );
  }
}

export default Messages;
