import React, { Component } from "react";
import GetUserByID from "../../GetUserByID";

import FriendsContact from "./FriendsContact/Index";
import LeftSideBar from "./LeftSideBar/Index";
import Content from "./Content/Index";
import Messages from "./Messages/Index";
import axios from "axios";
import { URL } from "../../../constants/url";

class Main extends Component {
  constructor(props) {
    super(props);
    this.state = {
      menuSelected: props.menuSelected,
      isShowGroupBox: false,
      isCreateGroup: false,
      isShowMessageBox: false,
      groupSelected: "",
      messagesInfo: {
        friendInfo: {},
        messages: [],
      },
    };
    this.handleMenuSelected = this.handleMenuSelected.bind(this);
    this.handleShowGroupBox = this.handleShowGroupBox.bind(this);
    this.handleCreateGroup = this.handleCreateGroup.bind(this);
    this.handleGroupSelected = this.handleGroupSelected.bind(this);
    this.handleShowMessageBox = this.handleShowMessageBox.bind(this);
    this.handleFriendMessage = this.handleFriendMessage.bind(this);
  }

  componentDidUpdate(prevProps) {
    if (prevProps.menuSelected !== this.props.menuSelected) {
      this.handleMenuSelected(this.props.menuSelected);
    }
  }

  handleMenuSelected(type) {
    this.setState({
      menuSelected: type,
    });
  }

  handleShowGroupBox(status) {
    this.setState({
      isShowGroupBox: status,
    });
  }

  handleCreateGroup() {
    this.setState({
      isCreateGroup: true,
    });
  }

  handleGroupSelected(groupID) {
    this.setState({
      groupSelected: groupID,
    });
  }

  handleShowMessageBox(status) {
    this.setState({
      isShowMessageBox: status,
    });
  }

  async handleFriendMessage(friendID) {
    const { userInfo } = this.props;

    const friendInfo = await GetUserByID(friendID);
    if (friendInfo.status === "success") {
      const messenger = await axios
        .post(URL + "/messages", {
          userID_1: userInfo._id,
          userID_2: friendID,
          message: "",
        })
        .then((res) => res.data);

      if (messenger.status === "success") {
        this.setState({
          messagesInfo: {
            friendInfo: friendInfo.doc,
            messages: messenger.doc.messages,
          },
          isShowMessageBox: true,
        });

        return;
      }

      this.setState({
        messagesInfo: {
          ...this.state.messagesInfo,
          friendInfo: friendInfo.doc,
        },
        isShowMessageBox: true,
      });
    }
  }

  render() {
    const {
      userInfo,
      isUpdateFriend,
      handleFriendStatus,
      handleAlert,
    } = this.props;

    return (
      <main id="main" className="d-flex bg-color-light-2 overflow-x-hidden">
        <LeftSideBar
          menuSelected={this.state.menuSelected}
          handleShowGroupBox={this.handleShowGroupBox}
          isCreateGroup={this.state.isCreateGroup}
          handleGroupSelected={this.handleGroupSelected}
        />
        <div className="mr-auto col-lg-3 col-0 main-sub"></div>

        <Content
          userInfo={userInfo}
          handleShowGroupBox={this.handleShowGroupBox}
          isShowGroupBox={this.state.isShowGroupBox}
          handleAlert={handleAlert}
          handleCreateGroup={this.handleCreateGroup}
          groupSelected={this.state.groupSelected}
        />

        <div className="ml-auto col-lg-3 col-md-4 col-0 main-sub"></div>
        <FriendsContact
          userInfo={userInfo}
          isUpdateFriend={isUpdateFriend}
          handleFriendStatus={handleFriendStatus}
          handleFriendMessage={this.handleFriendMessage}
        />

        {this.state.isShowMessageBox ? (
          <Messages
            messagesInfo={this.state.messagesInfo}
            handleShowMessageBox={this.handleShowMessageBox}
          />
        ) : (
          ""
        )}
      </main>
    );
  }
}

export default Main;
