import React, { Component } from "react";
import axios from "axios";

import Login from "./Login";
import Signup from "./Signup";
import Alert from "../Alert";

import { URL } from "../../constants/url";

class UserPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loginTab: true,
      alert: {
        alertType: "",
        alertStatus: false,
        alertMessage: "",
      },
    };

    this.handleAlert = this.handleAlert.bind(this);
    this.handleUserPage = this.handleUserPage.bind(this);
    this.handleLogin = this.handleLogin.bind(this);
    this.handleSignup = this.handleSignup.bind(this);
  }

  handleUserPage(boolean) {
    this.setState({
      ...this.state,
      loginTab: boolean,
    });
  }

  handleLogin(email, password) {
    if (!email) {
      this.handleAlert("warning", "Bạn chưa nhập Email.");
      return;
    }

    if (!password) {
      this.handleAlert("warning", "Bạn chưa nhập mật khẩu.");
      return;
    }

    axios
      .post(`${URL}/users`, {
        type: "login",
        userInfo: {
          email,
          password,
        },
      })
      .then((res) => res.data)
      .then((data) => {
        if (data.status === "failed") {
          this.handleAlert("danger", data.message);
        } else {
          this.props.handleLogin(true, data.doc._id);
        }
      });
  }

  handleSignup(userInfo) {
    const { firstName, lastName, email, password, birthday, gender } = userInfo;

    if (!firstName) {
      this.handleAlert("warning", "Bạn chưa nhập họ.");
      return;
    }

    if (!lastName) {
      this.handleAlert("warning", "Bạn chưa nhập tên.");
      return;
    }

    if (!email) {
      this.handleAlert("warning", "Bạn chưa nhập Email.");
      return;
    }

    if (!password) {
      this.handleAlert("warning", "Bạn chưa nhập mật khẩu.");
      return;
    }

    if (!birthday) {
      this.handleAlert("warning", "Bạn chưa nhập ngày tháng năm sinh.");
      return;
    }

    if (!gender) {
      this.handleAlert("warning", "Bạn chưa chọn giới tính.");
      return;
    }

    axios
      .post(`${URL}/users`, {
        type: "signup",
        userInfo,
      })
      .then((res) => res.data)
      .then((data) => {
        if (data.status === "failed") {
          this.handleAlert("danger", data.message);
        } else {
          window.location.reload();
        }
      });
  }

  handleAlert(alertType, alertMessage) {
    this.setState({
      ...this.state,
      alert: {
        alertType,
        alertMessage,
        alertStatus: true,
      },
    });
  }

  render() {
    return (
      <div className="container">
        {this.state.alert.alertStatus ? (
          <Alert
            alertType={this.state.alert.alertType}
            alertMessage={this.state.alert.alertMessage}
          />
        ) : (
          ""
        )}
        <div className="d-flex justify-content-center h-100">
          <div className="h-100 m-0 pb-5 px-5">
            <div className="card login-logo">
              <img
                src="https://www.gapo.vn/assets/images/logo%20gapo.svg"
                alt=""
              />
            </div>
            {this.state.loginTab ? (
              <Login
                handleUserPage={this.handleUserPage}
                handleLogin={this.handleLogin}
              />
            ) : (
              <Signup
                handleUserPage={this.handleUserPage}
                handleSignup={this.handleSignup}
              />
            )}
          </div>

          <div
            className="m-0 bg-success background"
            style={{ minHeight: "100vh" }}
          >
            <img
              className="w-100"
              src="https://www.gapo.vn/assets/images/right-cover.jpg"
              alt=""
            />
          </div>
        </div>
      </div>
    );
  }
}

export default UserPage;
