import React, {Component} from 'react'
import classNames from 'classnames'

class Angry extends Component{
    updateReaction(){
        const {content, handleUpdateReaction} = this.props

        if(!content){
            handleUpdateReaction('sad')
        }
    }

    render(){
        const {content} = this.props

        return(
            <div onClick={()=>{this.updateReaction()}}  
            className={classNames('d-flex', 'align-items-center', 'justify-content-center', 'w-100', 'h-100', {'reaction-icon': !content})}>
                <img
                    className={classNames({'mr-3': content, 'circle-auto': content, 'circle': !content})} 
                    src="http://gapo.vn/assets/images/png-icons/sad-reaction.png" alt="" />
                
                {content ? content : ''}
            </div>
        )
    }
}

export default Angry