import React, {Component} from 'react'
import FriendStatus_False from './FriendStatus/False/Index'
import FriendStatus_Responding from './FriendStatus/Responding/Index'
import FriendStatus_Requesting from './FriendStatus/Requesting/Index'

class ResultItem extends Component{
    constructor(){
        super()
        this.state = {
            friendStatus: ''
        }
        this.handleFriendStatus = this.handleFriendStatus.bind(this)
    }

    componentWillMount(){
        const { user, _id } = this.props
        
        user.friends.map(friend => {
            if(friend._id === _id){
                if(friend.status === 'requesting'){
                    this.setState({
                        friendStatus: 'responding'
                    })
                    return
                }

                if(friend.status === 'responding'){
                    this.setState({
                        friendStatus: 'requesting'
                    })
                    return
                }

                if(friend.status === true){
                    this.setState({
                        friendStatus: friend.status
                    })
                    return
                }
            }
        })
    }

    handleFriendStatus(status){
        this.setState({
            friendStatus: status
        })

        if(status === true){
            const {isUpdateFriend} = this.props
            isUpdateFriend(true)
        }
    }

    render(){
        const { user, _id } = this.props
        
        return(
            <div className="mb-1 p-2 d-flex align-items-center rounded hover-gray cursor-pointer user--info">
                <img className="circle mr-3 border" src={user.avatar} alt="" />
                <span className="fw-6">{user.firstName} {user.lastName}</span>
                {this.state.friendStatus === 'responding' ? <FriendStatus_Responding userID={_id} friendID={user._id} handleFriendStatus={this.handleFriendStatus} /> : this.state.friendStatus === 'requesting' ? <FriendStatus_Requesting userID={_id} friendID={user._id} handleFriendStatus={this.handleFriendStatus}/> : 
                this.state.friendStatus == true ? '' : <FriendStatus_False userID={_id} friendID={user._id} handleFriendStatus={this.handleFriendStatus} /> }
            </div>
        )
    }
}

export default ResultItem