import React, {Component} from 'react'
import classNames from 'classnames'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faBell, faUserFriends } from '@fortawesome/free-solid-svg-icons'

import UserCard from './UserCard/Index'
import SearchBar from './SearchBar/Index'
import HomeMenu from './Menu/Home'
import GroupsMenu from './Menu/Groups'

class Header extends Component{
    constructor(){
        super()
        this.state = {
            isShowUserCard: false,
            menuSelected: 'home'
        }
    }

    componentDidUpdate(prevProps){
        if(prevProps.menuSelected !== this.props.menuSelected){
            this.setState({
                menuSelected: this.props.menuSelected
            })
        }
    }

    render(){
        const {handleLogin, userInfo, isUpdateFriend, handleMenuSelected} = this.props

        return(
            <header className="box-shadow">
                <div className="h-100 d-flex align-items-center">
                    <div className="h-100 d-flex align-items-center">
                        <img height="30px" src="https://www.gapo.vn/assets/images/logo.svg" alt="" />
                    </div>

                    <div className="h-50 mx-1 d-inline-flex border"></div>

                    <SearchBar _id={userInfo._id} isUpdateFriend={isUpdateFriend} />
                    
                    <ul className="h-100 col-0 col-xl-6  d-flex justify-content-center position-absolute position-center">
                        <li onClick={()=>{handleMenuSelected('home')}} className={classNames('h-100', 'pt-2', 'pb-1', 'col', 'd-flex', 'align-items-center', 'cursor-pointer', {'header-menu--selected': this.state.menuSelected === 'home'})}>
                            <div className="d-list-item mx-auto">
                                <HomeMenu menuSelected={this.state.menuSelected} />
                                <p className="fs-09 text-nowrap">Trang chủ</p>
                            </div>
                        </li>

                        <li className="h-100 pt-2 pb-1 col d-flex align-items-center cursor-pointer pointer-events-none header-menu">
                            <div className="d-list-item mx-auto">
                                <span className="mb-5 d-flex justify-content-center">
                                    <img className="header-menu-type-true" src="https://www.gapo.vn/assets/icons/news-default.svg" alt="" />
                                </span>
                                <p className="fs-09 text-nowrap">Tin tức</p>
                            </div>
                        </li>

                        <li onClick={()=>{handleMenuSelected('groups')}} className={classNames('h-100', 'pt-2', 'pb-1', 'col', 'd-flex', 'align-items-center', 'cursor-pointer', {'header-menu--selected': this.state.menuSelected === 'groups'})}>
                            <div className="d-list-item mx-auto">
                                <GroupsMenu menuSelected={this.state.menuSelected} />
                                <p className="fs-09 text-nowrap">Nhóm</p>
                            </div>
                        </li>

                        <li className="h-100 pt-2 pb-1 col d-flex align-items-center cursor-pointer pointer-events-none header-menu">
                            <div className="d-list-item mx-auto">
                                <span className="mb-5 d-flex justify-content-center">
                                    <img className="header-menu-type" src="https://www.gapo.vn/assets/icons/video-default.svg" alt="" />
                                </span>
                                <p className="fs-09 text-nowrap">Video</p>
                            </div>
                        </li>

                        <li className="h-100 pt-2 pb-1 col d-flex align-items-center cursor-pointer pointer-events-none header-menu">
                            <div className="d-list-item mx-auto">
                                <span className="mb-5 d-flex justify-content-center">
                                    <img className="header-menu-type" src="https://www.gapo.vn/assets/icons/chat-default.svg" alt="" />
                                </span>
                                <p className="fs-09 text-nowrap">Chat</p>
                            </div>
                        </li>
                    </ul>

                    <ul className="h-100 col-0 col-md-4 ml-auto d-flex flex-row-reverse align-items-center">
                        <li className="ml-3 d-flex px-2 align-items-center cursor-pointer header-user" onClick={()=>{
                            this.setState({
                                isShowUserCard: !this.state.isShowUserCard
                            })
                        }}>
                            <img className="mr-3 circle border" src={userInfo.avatar} alt="" />
                            <div className="mr-1 fw-6 text-nowrap text-secondary username">{userInfo.firstName} {userInfo.lastName}</div>
                            <img src="https://www.gapo.vn/assets/icons/icon-dropdown.svg" alt="" />
                        </li>

                        {this.state.isShowUserCard ? <UserCard userInfo={userInfo} handleLogin={handleLogin} /> : ''}

                        <li className="mx-3 circle d-flex align-items-center justify-content-center border bg-color-light-1">
                            <FontAwesomeIcon icon={faBell} />
                        </li>
                        <li className="mx-3 circle d-flex align-items-center justify-content-center border bg-color-light-1">
                            <FontAwesomeIcon icon={faUserFriends} />
                        </li>
                    </ul>
                </div>
            </header>
        )
    }
}

export default Header