import React, { Component } from "react";

class Group extends Component{
    render(){
        const {groupInfo, handleGroupSelected} = this.props

        return(
            <li onClick={()=>{handleGroupSelected(groupInfo._id)}} className="p-3 d-flex align-items-center rounded cursor-pointer group-item hover-gray transition-03">
                <img width="56px" height="56px" className="mr-3 rounded" src={groupInfo.avatar} alt="" />
                {groupInfo.name}
            </li>
        )
    }
}

export default Group