import React, {Component} from 'react'
import Home from './MenuType/Home/Home'
import Groups from './MenuType/Groups/Index'

class LeftSideBar extends Component{
    render(){
        const {menuSelected, handleShowGroupBox, isCreateGroup, handleGroupSelected} = this.props

        return(
            <aside className="col-lg-3 col-0 bg-light position-fixed main-side--right">
                {menuSelected === 'home' ? <Home /> : menuSelected === 'groups' ? <Groups handleShowGroupBox={handleShowGroupBox} isCreateGroup={isCreateGroup} handleGroupSelected={handleGroupSelected}/> : ''}
            </aside>
        )
    }
}

export default LeftSideBar